body {
    margin: 0;
    padding: 0;
    background: rgb(82,92,135);
    background: linear-gradient(90deg, rgba(82,92,135,1) 0%, rgba(49,114,164,1) 100%, rgb(4, 186, 222) 100%); 
    background-size: cover;
    background-position: center;
    font-family: Arial, sans-serif;
    color: black; /* Text color for better readability */
  }
  
  .container {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
    border-radius: 8px;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    color: white;
  }

  img{
     width: 300px;
     height: 300px; 
  }

  .search-box {
	margin-bottom: 20px;
	margin-right: 0px;
  width: 100%;
}
.search-box input{
	padding: 1px;
  width: 95%;
	
}
  
  /* Add some spacing between the selects */
  select {
    margin: 5px 0;
  }
  
.model-details {
  display: flex;
  flex-direction: column;

  margin-top: 20px;
}

.model-details img {
  
  margin-bottom: 10px;
}

.model-details h3 {
  margin-bottom: 10px;
}

.model-details p {
  margin: 5px 0;
}